.hook {
}

.hook__text {
  margin: 0;
  margin-bottom: 30px;

  font-size: 24px;
  line-height: 1.2;
  font-weight: 300;
  color: #283645;
  text-align: center;
}

.hook__text:last-child {
  margin-bottom: 0;
}

@media (min-width: $tablet) {
  .hook__text {
    font-size: 40px;
  }
}

@media (min-width: $desktop) {
  .hook__text {
    font-size: 50px;
  }
}
